      icon="el-icon-back"
      ><router-link :to="{ name: 'home' }">返回首页</router-link></el-button
    >
  </div>
</template>
<script>
import Valine from "valine";
import setForm from "../components/create-aside/set.vue";
import FooterReward from "../components/FooterReward.vue";
import lockMix from "../mixins/lock";
export default {
  components: { setForm, FooterReward },
  mixins: [lockMix],
  data() {
    return {
      hash: window.location.hash,
      id: this.$route.params.id,
      type: this.$route.params.type,
      type_bread: {
        time: {
          type: "#/lib/time",
          label: "时间组件",
        },
        eff: {
          type: "#/lib/eff",
          label: "效率组件",
        },
        others: {
          type: "#/lib/others",
          label: "其他组件",
        },
      },
      item: null,
      params: null,
      form: {
        black: 0,
        bg_color: "",
        font_color: "",
        theme: "",
        icon: "",
        background: 9,
      },
      created: false,
      is_theme_link: false, // 不同主题链接分离
      is_expired: false,
      lock: null,
      lockStatus: false,
    };
  },
  computed: {
    icon_preview: function () {
      return require(`../assets/logos/${this.form.icon}.png`);
    },
  },
  mounted() {
    console.log("mounted");
    this.getData();
    new Valine({
      el: "#vcomments",
      // other config
      appId: "wgD3lhLSAPHdLGH0gPy9Ep5n-gzGzoHsz",
      appKey: "DX5XDbJapqKFh8XT5myBkrcc",
      avatar: "hide",
      path: window.location.hash,
      visitor: true,
    });
  },
  methods: {
    getData() {
      const _this = this;
      const apis = {
        time: "resources",
        eff: "efficiency",
        others: "others",
      };
      this.$axios
        .get(`./${apis[_this.type]}.json?v=${Math.random()}`)
        .then((res) => {
          let times = res.data.list.filter((item) => !item.hide);
          let item = times.find((item) => item.id === Number(_this.id));
          if (!item) return _this.openFullScreen2("该组件不存在");
          if (item.expired) {
            item.isExpired = new Date() > new Date(item.expired);
          }
          if (item && !item.isExpired) {
            _this.item = item;
            _this.form.bg_color = item.bg_color;
            _this.form.font_color = item.font_color;
            _this.item.original_link = item.link;
            item.theme && (_this.form.theme = item.theme[0].key);
            if (item.theme && item.theme[0].link) {
              _this.is_theme_link = true;
            }
            if (item.icons) {
              _this.form.icon = item.icons[0].key;
            }
          } else {
            // _this.$router.go(-2);
            _this.is_expired = true;
            _this.openFullScreen2();
          }
          if (item.close) {
            _this.openFullScreen2();
          }
        });
    },
    // custom parameter list
    onCustomize(params) {
      this.created = false;
      this.params = params;
      this.previewLink();
    },
    previewLink(newV = this.form) {
      const _this = this;
      let _p = { ...newV, ...this.params },
        _link = _this.item.original_link;
      console.log(_p);
      if (_this.is_theme_link) {
        let _theme = _this.item.theme.find(
          (item) => item.key === _this.form.theme
        );
        _link = _theme.link;
      }
      let encrypted = _this.$crypto.aseEncryptParams(JSON.stringify(_p));
      _this.item.link = `${_link}?e=${encrypted}`;
      console.log(_this.item.link);
    },
    createLink() {
      this.created = true;
    },
    handleCopy() {
      const _this = this;
      splitbee.track("Create widget", {
        type: _this.type,
        id: _this.id,
      });
      this.$message({
        message: "创建成功，快去粘贴到📒笔记软件内体验一下吧",
        type: "success",
      });
    },
  },
  watch: {
    form: {
      handler(newV, oldV) {
        this.previewLink(newV);
      },
      deep: true,
    },
  },
};
</script>
